.btn {
  animation: moveInBottom 1s ease-out 0.75s;
  animation-fill-mode: backwards;
  /* 
  Initial State of pseudo element
  .btn = parent
  .btn::after = child
  */
  /* Only Discover our tour button will have animation */
}
.btn, .btn:link, .btn:visited {
  text-transform: uppercase;
  text-decoration: none;
  padding: 0.75rem; /* padding: 15px 40px */
  border-radius: 6.25rem; /* 100px */
  transition: all 0.1s ease-in;
  position: relative;
  font-size: 1.1rem;
  border: none;
  cursor: pointer;
  box-shadow: 0.4rem 0.4rem 0.4rem rgba(220, 240, 255, 0.8);
}
.btn:hover {
  transform: scale(1.005) translateY(-0.15rem);
  box-shadow: 0.5rem 0.5rem 0.5rem 0.5rem rgba(220, 230, 250, 0.3), -0.5rem -0.5rem -0.5rem -0.5rem rgba(255, 215, 0, 0.3);
  /* box-shadow: offset-x offset-y blur rgba; */
}
.btn:hover::after {
  /* 
  2nd State of pseudo element
  pseudo element after for .btn:hover state
  Make pseudo element grow && fade-out 
  .btn::after = parent
  .btn:hover::after = child
  */
  transform: scaleX(1.4) scaleY(1.6);
  opacity: 0;
}
.btn:active, .btn:focus {
  outline: none;
  transform: translateY(-0.0625rem);
  box-shadow: 0.25rem 0.5rem 1rem rgba(220, 230, 250, 0.3);
}
.btn--white {
  background-color: #fff;
  color: rgb(59, 78, 147);
}
.btn--white::after {
  background-color: white;
  color: rgb(84, 87, 37);
}
.btn--green {
  background-color: rgb(157, 169, 225);
  color: #fff;
}
.btn--green::after {
  background-color: rgb(157, 169, 225);
}
.btn::after {
  /* Must declare content && display properties */
  content: "";
  display: inline-block;
  /* pseudo elements are treated as children */
  height: 100%;
  width: 100%;
  border-radius: 10rem; /* 100px */
  position: absolute;
  top: 0;
  left: 0;
  /* position: absolute need a reference 
  from parent box with position: relative
  In this case:
  .btn::after (child) 
  .btn (parent)
  */
  z-index: -1;
  transition: all 0.4s ease;
  -webkit-transition: all 0.4s ease;
}
.btn--animated {
  animation: moveInBottom 1s ease-out 0.75s;
  animation-fill-mode: backwards;
}

.btn-text:link, .btn-text:visited {
  font-size: 1.1rem;
  color: rgb(157, 169, 225);
  display: inline-block;
  text-decoration: none;
  border-bottom: 1px solid rgb(157, 169, 225);
  padding: 3px;
  transition: all 0.15s;
}
.btn-text:hover {
  transform: translateY(-0.05rem);
  background-color: rgb(177, 189, 235);
  color: #fff;
  box-shadow: 0 1rem 2rem rgba(255, 215, 0, 0.3);
  border-radius: 1rem;
}
.btn-text:active {
  box-shadow: 0 1rem 2rem rgba(220, 230, 250, 0.3);
  transform: translateY(0);
}

.btn-secondary {
  animation: moveInBottom 1s ease-out 0.75s;
