.heading-primary {
  color: #fff;
  text-transform: uppercase;
  /* Remove shaky behaviour in children animations */
  backface-visibility: hidden;
  margin-bottom: 6rem; /* 60px */
}
.heading-primary--main {
  display: block; /* <span> is inline by default */
  font-size: 6rem; /* 60px */
  font-weight: 400;
  letter-spacing: 3.5rem; /* 35px */
  /* Animation */
  animation-name: moveInLeft;
  animation-duration: 6s;
  animation-iteration-count: 1;
  animation-timing-function: ease-out;
}
.heading-primary--sub {
  display: block;
  font-size: 2rem; /* 20px */
  font-weight: 700;
  letter-spacing: 1.74rem; /* 17.4px */
  /* Animation */
  animation-iteration-count: 1;
  animation: moveInRight 6s ease-out;
}

.heading-secondary {
  font-size: 3.5rem;
  text-transform: uppercase;
  font-weight: 700;
  background-image: linear-gradient(to right, rgb(177, 189, 235), rgb(137, 149, 205));
  display: inline-block;
  -webkit-background-clip: text;
  color: transparent;
  letter-spacing: 2px;
  transition: all 0.2s;
}
.heading-secondary:hover {
  transform: skewY(2deg) skewX(15deg) scale(1.1);
  text-shadow: 0.5rem 1rem 2rem rgba(0, 0, 0, 0.2);
}

.heading-tertiary {
  font-size: 1.1rem;
  font-weight: 700;
  text-transform: uppercase;
}

.paragraph {
  font-size: 1.1rem;
}
.paragraph:not(:last-child) {
  margin-bottom: 3rem;
}

/*
0 - 600px:      Phone
600 - 900px:    Tablet portrait
900 - 1200px:   Tablet landscape
[1200 - 1800] is where our normal styles apply
1800px + :      Big desktop

$breakpoint arguement choices:
- phone
- tab-port
- tab-land
- big-desktop

ORDER: Base + typography > general layout + grid > page layout > components

1em = 16px
*/
.row {
  /*
  If we have enough space, we will have the specified width
  If we have insufficient space, we will have width: 100%;
  */
  margin: 0 auto;
  /*
  To apply effects to all columns
  using attributes selector
  e.g. 
  [alt="Logo"] {
  }
  */
}
.row:not(:last-child) {
  margin-bottom: 1rem;
}
.row::after {
  content: "";
  display: table;
  clear: both;
}
.row [class^=col-] {
  float: left;
}
.row [class^=col-]:not(:last-child) {
  margin-right: 5rem;
}
.row .col-1-of-2 {
  width: calc((100% - 5rem) / 2);
}
.row .col-1-of-3 {
  width: calc((100% - 2* 5rem) / 3);
}
.row .col-1-of-4 {
  width: calc((100% - 3* 5rem) / 4);
}
.row .col-2-of-3 {
  width: calc(2*((100% - 2* 5rem)/3) + 5rem);
}
.row .col-2-of-4 {
  width: calc(2*((100% - 3* 5rem)/4) + 5rem);
}
.row .col-3-of-4 {
  width: calc(3*((100% - 3* 5rem)/4) + 2*5rem);
}

.row-no {
  margin: 0 auto;
  margin-bottom: 0rem;
}

